import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import {
    IconTextCellParams,
    IIconCell,
    ITextWithIconCell,
    ITooltipCell,
} from '../../../../types/table-cells';

export interface ITextIconCellParams extends IconTextCellParams, ITooltipCell {
    icon_position?: 'before' | 'after';
}

type InitParams = ITextIconCellParams;

/**
 * @category Table Cell
 * @description A cell component for displaying a text with an icon.
 */

@Component({
    tag: 'atui-text-icon-cell',
    shadow: false,
})
export class AtuiTextIconCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() value: ITextWithIconCell;
    @State() params: InitParams;

    init(params: InitParams): void {
        this.params = params;
        this.value = params.value;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ICellRendererParams): boolean {
        this.updateCell(params);
        return true;
    }

    private updateCell(params: ICellRendererParams) {
        this.params.value = this.value = params.value;
    }

    getIconClick(icon: IIconCell): any {
        return icon.iconClick && icon.iconClick(this.params);
    }

    getTextClick(cell: ITextWithIconCell): any {
        return cell.click && cell.click(this.params);
    }

    get getIcons(): any {
        return this.params.value.icons.map((icon: IIconCell) => (
            <span
                class={`material-icons text-icon-md ${icon.iconClass}`}
                onClick={this.getIconClick(icon)}
            >
                {icon.iconName}
            </span>
        ));
    }

    render() {
        const iconPosition = this.params.icon_position || 'before';

        return (
            <Host class="flex items-center overflow-hidden">
                <atui-tooltip
                    position="right"
                    is_visible={!!this.params.generateTooltip}
                >
                    <div
                        class="flex items-center gap-8"
                        slot="tooltip-trigger"
                        onClick={this.getTextClick(this.params.value)}
                    >
                        {iconPosition === 'before' && this.getIcons}
                        <span class="truncate">
                            {this.value.text.textValue}
                        </span>
                        {iconPosition === 'after' && this.getIcons}
                    </div>

                    {this.params.generateTooltip && (
                        <span
                            slot="tooltip-content"
                            class={`leading-normal`}
                        >
                            {this.params.generateTooltip(this.params)}
                        </span>
                    )}
                </atui-tooltip>
            </Host>
        );
    }
}
